.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ant-menu-horizontal {
  line-height: 25px;
  border: 0;
  border-bottom: 0px;
  box-shadow: none;
}

.ant-layout-header {
  height: 15px;
  padding: 20px;
  color: #212121;
  line-height: 30px;
  background: #ffffff;
  margin: 10px !important;
}

.ant-menu li {
  color: #212121;
  font-weight: 700;
  display: inline-block;
  line-height: 1.25;
  text-transform: uppercase;
}

.ant-menu-item-selected {
  color: #ff6f61 !important;
  border-bottom: 0px !important;
}

.ant-menu img {
  height: 30px !important;
}

.ant-menu-item-active {
  color: #ff6f61 !important;
  border-bottom: 0px !important;
}

.ant-menu-item:hover {
  color: #ff6f61 !important;
  border-bottom: 0px !important;
  text-decoration: none;
}

.banner {
  background-color: #f7f9fc;
  padding: 15px 35px;
}

.heading {
  padding: 10px 40px;
  border-bottom: 1px solid #f1f4f6;
}

.logintext {
  padding: 10px 40px;
}

.logintext {
  padding: 14px;
  line-height: 29px;
}

.ant-btn-dangerous.ant-btn-primary {
  color: #fff;
  background: #ff4d4f;
  border-color: #ff4d4f;
  text-shadow: 0 -1px 0 rgb(0 0 0 / 12%);
  box-shadow: 0 2px 0 rgb(0 0 0 / 5%);
  margin: 0px 15px;
}

.lower-header {
  padding: 10px;
}

.upper-btn {
  margin: 0px 100px;
}

.icon-1 {
  width: 50% !important;
  height: 50% !important;
  text-align: center !important;
}

.left-banner {
  padding: 15px;
}

.lab-test-heading {
  font-size: 24px;
  font-weight: 700;
  color: #212121;
  text-align: left;
}

.para-below {
  font-size: 16px;
  color: #212121;
  text-align: left;
}

.icons-row {
  padding: 15px;
  text-align: center !important;
}

.img-cover {
  padding: 30px;
}

h1 {
  margin-top: 0;
  line-height: 1;
  margin-bottom: 30px;
  opacity: 0.87;
  font-size: 21px;
  font-weight: 700;
  color: #212121;
  text-align: left;
}


.featured-row{
  background-color: #fbfbfb;
  padding:50px;
}

.fea-1{
  text-align: center;
  box-shadow: 0 0 2px 0 rgb(0 0 0 / 12%), 0 0 1px 0 rgb(0 0 0 / 12%);
  border-radius: 3px;
  width: 50%;
}

.quality-row{
  padding: 50px;
  margin:25px;
}

.quality-col{
  padding: 25px !important;
}

.customers{
  background: #fbfbfb;
}

.customers .ant-carousel {
  box-sizing: border-box;
    margin: 10px;
    padding: 10px 50px;
    color: rgba(0, 0, 0, 0.85);
    font-size: 14px;
    font-variant: tabular-nums;
    line-height: 0 !important;
    list-style: none;
}

.cust{
  padding: 10px;
    margin: 20px;
    text-align: center;
}

.what{
  margin: 30px 0;
  background-color: #fbfbfb;
  padding:30px;
}

.book-lab{
  font-size: 20px;
  font-weight: 700;
}

.keep{
  text-align: left;
  padding:40px;
}

.keep h3{
  font-weight: bold;
}

.largest{
  text-align: center !important;
  background-color: #fff;
    box-shadow: 2px 0 4px 0 rgb(0 0 0 / 12%);
